.services {
    &__content {
        position: relative;
        padding: 265px 0 320px;
        @include lg {
            padding: 108px 0 169px; }
        @include sm {
            padding: 65px 0 44px; }
        .services_secondary & {
          padding: 275px 0 280px;
          @include lg {
            padding: 165px 0 110px; } } }
    &__bg {
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        width: 50%;
        overflow: hidden;
        backface-visibility: hidden;
        @include lg {
            right: -2%;
            width: 50.5%; }
        @include sm {
            display: none; }
        .services_secondary & {
          top: 200px;
          bottom: 170px;
          @include lg {
            top: 132px;
            bottom: 70px;
            right: 0%;
            width: 42.5%; } } }
    // &__title
    //     margin-bottom: 35px
    //     +lg
    //         margin-bottom: 4px
    //     +sm
    //         margin-bottom: 0
    &__header {
        max-width: 400px; }
    &__tabs {
        margin-top: 110px;
        @include lg {
            margin-top: 69px; }
        @include sm {
            margin-top: 25px; }
        .services_secondary & {
            margin-top: 50px; } }
    &__sphere {
        position: absolute;
        &_lg {
            top: 113px;
            right: 119px;
            @include lg {
                top: 89px;
                right: 100px; } }
        &_sm {
            top: 273px;
            left: 80px;
            @include lg {
                top: 221px;
                left: 94px; } }
        &_xmd {
          top: 100px;
          left: 100px;
          @include lg {
            top: 54px;
            left: 60px; } }
        &_sm-secondary {
          top: 230px;
          right: 100px;
          @include lg {
            top: 172px;
            right: 70px; } } } }
